import React from 'react';
import Helmet from 'react-helmet';
import Img from 'gatsby-image';

import Location from '../components/Location';

const PlacesPage = ({ data, windowHeight, windowWidth }) => (
	<div>
    <Img
      style={{
        zIndex: -1,
        position: "fixed",
        right: 0,
        bottom: 0,
        width: windowWidth,
        height: windowHeight,
      }}
      sizes={data.backgroundImage.sizes}
    />
    <Helmet
      title="Bezugsquellen Privatpersonen | Ingredienza"
      meta={[
        { name: 'description', content: 'Wir sind Ingredienza – bei uns dreht sich alles um vielfältige Pastakreationen und hochwertige Zutaten. Unser Sortiment gibt es nun auch für Privatpersonen an unterschieldlichen Standorten in Bern. ► Entdecken Sie unsere Standorte' },
        { name: 'keywords', content: 'Ingredienza'}
      ]}
    />
		<h1>Bezugs&shy;quellen für Privat&shy;personen</h1>
		<p>
			Nicht nur Gastrobetriebe, sondern auch Private sollen bei uns auf ihre
			Kosten kommen. Deshalb bieten wir unsere feine Pasta und köstlichen Gnocchi an
			unterschiedlichen Standorten in Bern an, wo Sie als Privatkunde einen Teil
			unseres Sortiments direkt beziehen können. 
		</p>
		<p>
      Kommen Sie in unserer <strong>Teigwaren-Manufaktur an der Güterstrasse
      7</strong> vorbei, wo wir von Montag bis Freitag unser Kernsortiment in
      Portionen abgepackt anbieten. Sie haben ebenfalls die Möglichkeit, Gastroeinheiten zu Gastropreisen zu kaufen. <br />
			Öffnungszeiten Fabrikladen: Montag bis Freitag 08:00 bis 16:00 Uhr <br />an der
			Güterstrasse 7, 3008 Bern
		</p>
		<p>
			Auch auf dem <strong>Wochenmarkt in Bern</strong> sind wir vertreten. Entdecken Sie eine
			breite Auswahl unserer Pasta und Gnocchi jeweils samstags zwischen 07:30 und 12:00 Uhr
			auf dem Bundesplatz vor der Nationalbank. 
		</p>
		<p>
      Unsere Produkte sind zudem auch in der Berner Altstadt im gleichnamigen 
      <strong> „Ingredienza der Laden“</strong> erhältlich. Dort finden Sie neben
      italienischen Delikatessen auch unsere hausgemachte Pasta und Gnocchi aus
      Bern. <br /> 
			Öffnungszeiten Ingredienza der Laden: Dienstag bis Freitag 09.30 bis 19.00
			Uhr<br /> und Samstag 08.00 bis 17.00 Uhr an der Kramgasse 80, 3011 Bern.
		</p>
    <h2> Weitere Verkaus&shy;standorte </h2>
    <Location
      name="ängelibeck Boulangerie und Grand Café"
      location="Könizstrasse 291, 3089 Köniz"
      hours={{
        week: {
          description: "Mo. - Fr.",
          times: "06:30 – 19:00 Uhr",
        },
        weekend: "07:30 – 17:00 Uhr"
      }}
    />      
     
    <Location
      name="Hofladen und ängelibeck"
      location="Schüpfenried, 3043 Uettligen"
      hours={{
        week: {
          description: "Mo. - Fr.",
          times: "07:30 - 18:30 Uhr",
        },
        weekend: "07:00 - 17:00 Uhr"
      }}
    />      

     
    <Location
      name="Petit Couteau AG"
      location="Monbijoustrasse 6, 3011 Bern"
      hours={{
        week: {
          description: "Di. - Fr.",
          times: "11:00 - 19:00 Uhr",
        },
        weekend: "10:00 - 16:00 Uhr"
      }}
    />      

    <Location
      name="Bio-Metzgerei Wegmüller AU COCHON ROSE"
      location="Länggassstrasse 36, 3012 Bern"
      hours={{
        week: {
          description: "Mo. - Fr.",
          times: "08:00 - 12:30 Uhr\n14:30 - 18:30 Uhr",
        },
        weekend: "08:00 - 16:00 Uhr",
        special: "Mittwoch ganzer Tag geschlossen",
      }}
    />      
	</div>
)
// TODO: Linespacing 
export const query = graphql`
  query PlacesImageQuery {
    backgroundImage: imageSharp(id: { regex: "/placesBg/" }) {
      sizes(maxWidth: 2300 ) {
        ...GatsbyImageSharpSizes
      }
    }
  }
`;


export default PlacesPage;




