import React from 'react';

import styles from '../../styles/location.module.css';
const Location = ({ name, location, hours }) =>
  <div className={styles.container}>
    <div className={styles.name}>
      <strong>{ name }</strong>
    </div>
    <div className={styles.location}>
      { location }
    </div>
    <div className={styles.hours}>
      <div className={styles.hoursDescription}>
        Öffnungszeiten:
      </div>
      <div className={styles.weekDescription}>
        { hours.week.description }
      </div>
      <div className={styles.weekHours}>
        { hours.week.times }
      </div>
      <div className={styles.weekendDescription}>
        Sa.
      </div>
      <div className={styles.weekendHours}>
        { hours.weekend }
      </div>
      { hours.special ?
        (<div className={styles.hoursSpecial}>
          <strong>{hours.special}</strong>
        </div>) 
        : (<div />)
      }
    </div>
  </div>  


export default Location;



